html, body {
    height: 100%;
    margin: 0px;
}
.App {
    height: 100%;
}

#root {
    height: 100%;
}

* {
    box-sizing: border-box;
}

.marker {
    display: block;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    padding: 0;
    z-index: 100;
    background-image: url('http://com.capelon.city-vision.s3-website.eu-north-1.amazonaws.com/images/+100_5.svg');
    background-repeat: no-repeat;
    background-size: contain;
    border: 1px solid red;
}

.mapboxgl-popup {
    color: white;
}
.mapboxgl-popup-content {
    min-width: 180px;
    background-color: #4472C4;
    padding-top: 10px;
}

.mapboxgl-popup-close-button {
    font-size: 30px;
    color: white;
    display: block;
}

.mapboxgl-popup-tip {
    border-top-color: #4472C4!important;
}

.popup-menu {
    padding: 4px;
    color: white;
    background-color: #4472C4;
    padding-top: 20px;
}

.popup-menu a {
    color: white;
}

.popup-menu a:hover {
    color: lightblue;
}